import http from '../../common/http.js'
import amap from '../../common/amap-wx.js'
export default {
	data() {
		return {
			status: 'loadmore',
			iconType: 'flower',
			loadText: {
				loadmore: '上拉加载更多',
				loading: '努力加载中',
				nomore: '没有更多了'
			},
			swiperList: null,
			sortId: 'all',
			imgUrl: http.imgUrl,
			dotStyle: 1,
			menuList: [],
			address: '',
			amapPlugin: null,
			screenIndex: 1,
			key: '6357e894cdbb23207ebbd23dc821232b',
			posName: uni.getStorageSync('posName'),
			lat: uni.getStorageSync('lat'),
			lng: uni.getStorageSync('lng'),
			pageNum: 1,
			shopList: [],

			star: '',
			priceSort: '',
			cate_id: '',
			sell_num: '',
			nearMe: '',
			list_tag: ''

		}
	},
	onLoad: function() {
		uni.showLoading({
			title: '加载中'
		})
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		});

	},
	onShow: function() {
		this.allRequest()
	},
	onReady: function() {
		uni.hideLoading()
	},
	onPullDownRefresh: function() {
		console.log('触发下拉刷新')
		this.posName = uni.getStorageSync('posName'),
			this.lat = uni.getStorageSync('lat'),
			this.lng = uni.getStorageSync('lng'),
			this.shopList = []
		this.allRequest()
		uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {
		++this.pageNum
		this.getShop()
		console.log('触发上拉')
	},
	methods: {

		allRequest: function() {
			console.log(this.lat)
			console.log(this.lng)
			console.log(this.posName)
			if (this.lat == '' || this.lat.length < 1 || this.lng == '' || this.posName == '') {
				console.log('zhixingdingwei')
				this.getRegeo()
			}
			this.lat = uni.getStorageSync('lat'),
				this.lng = uni.getStorageSync('lng'),
				this.posName = uni.getStorageSync('posName'),
				this.getBanner()
			this.getNavigatorMenu()
			this.getShop()
		},
		getRegeo: function() {
			this.amapPlugin.getRegeo({
				success: (data) => {
					console.log(data)
					this.cityName = data[0].regeocodeData.addressComponent.city
					uni.setStorageSync('lat', data[0].latitude);
					uni.setStorageSync('lng', data[0].longitude);
					uni.setStorageSync('posName', data[0].regeocodeData.addressComponent.city);

				}
			});
		},

		maskClick: function() {
			console.log("masins")
			this.sortShow = false
			console.log(this.sortShow)
		},
		goSearch: function() {
			// console.log('goSearch')
		},
		//点击排序
		chooseSortType: function(e) {
			this.sortId = e.currentTarget.dataset.id
			this.star = ''
			this.priceSort = ''
			this.nearMe = ''
			this.sell_num = ''
			this.pageNum = 1
			this.list_tag = ''

			switch (e.currentTarget.dataset.id) {
				case 'star':
					this.star = 1
					break;
				case 'price1':
					this.list_tag = 1
					this.priceSort = 1
					break;
				case 'price2':
					this.list_tag = 1
					this.priceSort = 2
					break;
					// default:

			}

			this.getShop()
			this.sortShow = false
		},
		getBanner: function() {
			// console.log('banner')
			let opts = {
				url: 'index/getBanner',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				cate_id: '1'
			};
			http.httpTokenRequest(opts, param).then(res => {
				// console.log(res.data);
				let list = res.data.data
				for (let i in list) {
					list[i].image = this.imgUrl + list[i].pic
				}
				this.swiperList = list
				console.log(this.swiperList)
			}, error => {
				console.log(error);
			})

		},
		getNavigatorMenu: function() {
			let opts = {
				url: 'index/getNavigator',
				method: 'post'
			};
			let param = {
				city_id: '370700',
			};
			http.httpTokenRequest(opts, param).then(res => {
				// console.log(res.data);
				var menu_arr = res.data.data
				var menu_result = [];
				for (var i = 0; i < menu_arr.length; i += 10) { //将数组重组，每一个为一个单独的数组
					menu_result.push(menu_arr.slice(i, i + 10));
				}
				this.menuList = menu_result
				// console.log(menu_result.length)
			}, error => {
				console.log(error);
			})
		},
		//点击筛选
		clickScreen: function(e) {
			this.screenIndex = e.target.id
			uni.createSelectorQuery().select(".screen_box").boundingClientRect(data => { //目标节点
				uni.createSelectorQuery().select(".container").boundingClientRect((res) => { //最外层盒子节点
					uni.pageScrollTo({
						duration: 0, //过渡时间必须为0，uniapp bug，否则运行到手机会报错
						scrollTop: data.top - res.top, //滚动到实际距离是元素距离顶部的距离减去最外层盒子的滚动距离
					})
				}).exec()
			}).exec();

			if (e.target.id == 1) {
				this.sortShow = true
			} else {
				this.sortShow = false
			}
			if (e.target.id == 2 || e.target.id == 3) {
				this.star = ''
				this.priceSort = ''
				this.nearMe = ''
				this.sell_num = ''
				this.pageNum = 1
				this.list_tag = ''
				if (e.target.id == 2) {
					this.nearMe = 1
					this.list_tag = 1
				} else if (e.target.id == 3) {
					this.sell_num = 1
				}
				this.getShop()
			}
			console.log("dianji")
		},
		//获取店铺列表
		getShop: function() {
			this.status = 'loading'
			let opts = {
				url: 'index/shop',
				method: 'post'
			};
			let param = {
				pageNum: this.pageNum,
				city_id: 370700,
				lat: this.lat,
				lng: this.lng,
				star: this.star,
				priceSort: this.priceSort,
				nearMe: this.nearMe,
				sell_num: this.sell_num,
				list_tag: this.list_tag
			};
			console.log(param)
			http.httpTokenRequest(opts, param).then(res => {
				if (this.pageNum == 1) {
					this.shopList = []
				}
				this.shopList = this.shopList.concat(res.data.data)
				this.status = res.data.data.length ? 'loadmore' : 'nomore'
				console.log(this.shopList)
			}, error => {
				console.log(error);
			})
		},
		goNear: function() {
			uni.navigateTo({
				url: 'near'
			});
		},
		goShop: function(e) {
			// console.log(e)
			uni.navigateTo({
				url: "../shop/shop?shop_id=" + e
			})
		},
		moveHandle: function() {

		}

	},


}
